  --system-ui: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

body {
  font-family: var(--system-ui);
  margin: 0px;
}

h1,
h2 {
  margin: 0;
}

.detailContainer {
  margin-bottom: 25px;
  margin-top: 10px;
}

#createProjectBtn,
#addNewTodoBtn,
#addNewProjectBtn,
#cancelAddNewProjectBtn,
#saveTodoBtn,
#cancelAddTodoBtn {
  padding: 10px 15px;
  border-radius: 5px;
  border: none;
  color: white;
  font-weight: bold;
}

#addNewProjectBtn,
#cancelAddNewProjectBtn,
#saveTodoBtn,
#cancelAddTodoBtn {
  margin-top: 25px;
}

dialog {
  border-radius: 5px;
  border: none;
  padding: 30px;
}

#createProjectBtn:hover,
#addNewTodoBtn:hover,
#addNewProjectBtn:hover,
#cancelAddNewProjectBtn:hover,
#saveTodoBtn:hover,
#cancelAddTodoBtn:hover {
  padding: 13px 18px;
}

#createProjectBtn {
  background-color: rgb(179, 98, 179);
}

#addNewTodoBtn {
  background-color: rgb(130, 130, 216);
}

.projectChildContainer {
  margin-bottom: 15px;
}

.container,
.todoInfoContainer,
.todoBtnContainer,
.hideProjectDetailContainer,
#addNewProjectBtn,
#cancelAddNewProjectBtn,
.bodyContainer,
.actionSection {
  display: flex;
}

.headerSection {
  padding: 15px 20px;
  background-color: rgb(125, 162, 236);
  font-size: 20px;
  color: white;
}

.projectSection {
  margin-left: 200px;
  margin-top: 100px;
}

.bodyContainer {
  justify-content: space-between;
}

.actionSection {
  width: 500px;
  margin-left: 200px;
  margin-top: 100px;
}

.headerSection > h1 {
  margin-left: 50px;
}

.hideProjectDetailContainer {
  justify-content: end;
}

.todoTitleEditContainer,
.todoSelectPriorityContainer,
.todoDetailedDescContainer,
.todoNoteContainer,
#todoInputForm > div,
#projectInputForm > div {
  display: flex;
  align-items: center;
}

.container {
  justify-content: center;
}

.todoInfoContainer {
  align-items: center;
}

.todoInfoContainer,
.todoTitleEditContainer,
.todoSelectPriorityContainer,
.todoDetailedDescContainer,
.todoNoteContainer,
.todoBtnContainer,
#todoInputForm > div,
#projectInputForm > div,
.actionSection {
  gap: 15px;
}

.projectChildContainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 25px;
}

.todoContainer > div,
.todoTitleEditContainer,
.todoSelectPriorityContainer,
.todoDetailedDescContainer,
.todoNoteContainer,
#todoInputForm > div,
#projectInputForm > div {
  margin-bottom: 15px;
}

.todoContainer,
.projectContainer {
  border: 1px solid rgb(255, 255, 255);
  border-radius: 5px;
  padding: 25px;
}

.todoContainer {
  border: 1px solid rgb(228, 226, 226);
}

.todoBtnContainer {
  margin-top: 50px;
}

.todoSaveBtn,
.todoDeleteBtn,
.detailBtn,
.todoDetailBtn,
.hideProjectDetailBtn {
  height: 30px;
  width: 75px;
  border-radius: 5px;
  border: none;
  font-weight: bold;
  color: white;
}

.todoSaveBtn:hover,
.todoDeleteBtn:hover,
.detailBtn:hover,
.todoDetailBtn:hover,
.hideProjectDetailBtn:hover {
  width: 78px;
  height: 33px;
}

.todoDeleteBtn,
#cancelAddNewProjectBtn,
#cancelAddTodoBtn {
  background-color: #e34432;
}

.todoSaveBtn,
#addNewProjectBtn,
#saveTodoBtn {
  background-color: #809cff;
}

.todoDetailBtn,
.detailBtn {
  background-color: #ffcc80;
}

.detailBtn:disabled {
  background-color: #fff3e1;
}

.hideProjectDetailBtn {
  background-color: rgb(130, 197, 130);
}
